export default {
	props: {
		options: {
			type: Object,
			default: function(e) {
				return {}
			}
		}
	},
	methods: {
		bindClick() {
			this.$emit('click');
		}
	},
	filters:{
		formatType(type){
			switch (type){
				case 'ask':
				   return '问答'
					
				case 'share':
				     return '分享'
					 
				case 'job':
				     return '招聘'
					 
				case 'good':
				     return '精品'
				default:
					return '问答'
			}
		}
	}
}
